import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

export default function App() {
  const [age, setAge] = React.useState("");

  function menuuu(index) {
    const items = [];
    const keys = Object.keys(menu[index]);
    const values = Object.values(menu[index]);
    for (let i = 0; i < values.length; i++) {
      if (i == 0) {
        items.push(<img src={values[i]} alt="s" />);
      } else {
        items.push(<div>{values[i]}</div>);
      }
    }
    return <ul>{items}</ul>;
  }

  return (
    <>
      <div>
        <Accordion>
          <AccordionSummary
            expandIcon={<ExpandMoreIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
          >
            <Typography>Accordion 1</Typography>
          </AccordionSummary>
          <AccordionDetails>
            <div className="Kaigyo">
              <div className="Gun">{menuuu(0)}</div>

              <div className="Gun">{menuuu(0)}</div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>

      <h1 classname="KOU">高田皓介</h1>

      {menuuu(1)}
      {menuuu(2)}
      {menuuu(3)}
    </>
  );
}
